import * as actionTypes from './actions';

const initialState = {
    ingredients: {
        salad: 0,
        cheese: 0, 
        meat: 0, 
        bacon: 0
    },
    totalPrice: 4
}

const INGEDIENT_PRICES = {
    salad: 0.5,
    cheese: 0.4,
    meat: 1.3,
    bacon: 0.7
}

const reducer = (state = initialState, action) => {

    switch(action.type){
        case actionTypes.ADD_INGREDIENT:
            return {
                ...state,
                ingredients: {
                    ...state.ingredients,
                    [action.value]: state.ingredients[action.value] + 1
                },
                totalPrice: state.totalPrice + INGEDIENT_PRICES[action.value]
            }
        case actionTypes.REMOVE_INGREDIENT:
            return {
                ...state,
                ingredients: {
                    ...state.ingredients,
                    [action.value]: state.ingredients[action.value] - 1
                },
                totalPrice: state.totalPrice - INGEDIENT_PRICES[action.value]
            }
        default:
            return state;
    }
}

export default reducer;


